$color-primary: rgb(20, 20, 20);

$paddings: (
  xsmall: 0.5rem,
  small: 1rem,
  med: 1.5rem,
  large: 2rem,
  xlarge: 3rem,
  xxlarge: 5rem,
);

$height: 1000px;

$radius: (
  element: 5px,
  icon: 6px,
  circle: 50%,
);

$font-w-normal: 400;
$font-w-medium: 500;
$font-w-semibold: 600;
$font-w-bold: 700;

$breakpoints: (
  tablet: 768px,
  laptop: 1024px,
  desktop-med: 1280px,
  desktop-large: 1600px,
);

@mixin screen-sm {
  @media screen and (min-width: 1024px) and (max-width: 1280px) {
    @content;
  }
}

@mixin screen-xs {
  @media screen and (max-width: 1023px) {
    @content;
  }
}
